import {mapState, mapMutations} from 'vuex'
export default {
  name: 'pay',
  components: {},
  inject: ['reload'], // 引入方法
  data () {
    return {
      info: {
        licence: '沪A00001',
        startTime: '2018-12-12 12:24:00',
        parkTime: '121',
        amount: '40'
      },
      payMethod: 'score',
      paying: false
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.user.user_info,
      parkingInfo: state => state.user.parking_info
    })
  },
  watch: {
  },
  created () {
    if (typeof this.userInfo.memberId === 'undefined') {
      this.get_user_info()
    }

    if (typeof this.parkingInfo.carNo === 'undefined') {
      this.get_parking_data()
    }
  },
  mounted () {
  },
  destroyed () {
  },
  methods: {
    ...mapMutations([
      'get_user_info',
      'get_parking_data',
      'set_common_data_parking_rule'
    ]),
    switchPayMethod (method) {
      this.payMethod = method
    },
    doPay () {
      switch (this.payMethod) {
        case 'score':
          this.payByScore()
          break
        case 'weixin':
          this.payByWeiXin()
          break
        default:
          break
      }
    },
    payByScore () {
      if (this.paying) {
        return false
      }
      this.$vux.loading.show({
        text: '支付中'
      })
      this.paying = true

      let postData = {
        consumePoint: this.parkingInfo.fee * 100,
        platformMemberId: this.userInfo.memberId,
        operator: this.userInfo.memberId,
        type: 2,
        carNo: this.parkingInfo.carNo
      }

      this.$http.post(this.API.operationPoint, postData).then(res => {
        this.$vux.loading.hide()
        this.paying = false
        if (res.return_code === '0000') {
          this.$router.push('/pay_success?queryId=' + res.data.operationScoreId)
        } else {
          this.$router.push('/pay_fail/' + res.return_message)
        }
      })
    },
    payByWeiXin () {
      if (this.paying) {
        return false
      }
      this.$vux.loading.show({
        text: '支付中'
      })
      this.paying = true

      let postData = {
        tranAmt: this.parkingInfo.fee,
        carNo: this.parkingInfo.carNo
      }

      this.$http.post(this.API.wxPay, postData).then(res => {
        this.$vux.loading.hide()
        this.paying = false
        if (res.return_code === '0000') {
          if (res.data && typeof res.data.codeUrl === 'string') {
            window.location.href = res.data.codeUrl
          }
        } else {
          this.$router.push('/pay_fail/' + res.return_message)
        }
      })
    }
  }
}



